/* global variables */
.header {
  position: fixed;
  top: 0;
  width: 100%;
  font-family: 'GT America';
  font-weight: 300;
  z-index: 999; }

.burger {
  cursor: pointer; }

.buttonBox {
  position: absolute;
  top: 25px;
  right: 25px; }

.button {
  cursor: pointer;
  border-radius: 2px;
  border: 1px solid #5F2EC3;
  background-color: #401B8F;
  text-align: center;
  font-weight: lighter;
  font-size: 12.2px;
  color: rgba(255, 255, 255, 0.8);
  letter-spacing: 0.0854px;
  padding: 5px 22px 8px 22px;
  margin: 0 4px;
  -webkit-transition: 0.17s ease-in all;
  -o-transition: 0.17s ease-in all;
  transition: 0.17s ease-in all; }

.button:hover {
  opacity: 0.7; }

.awardBadge {
  position: fixed;
  bottom: 28px;
  left: 85px; }
  .awardBadge a {
    -webkit-transition: 0.3s ease-in all;
    -o-transition: 0.3s ease-in all;
    transition: 0.3s ease-in all; }
  .awardBadge a img {
    width: 80px; }
  .awardBadge a:hover {
    opacity: 0.7; }

.awardBadge2 {
  position: fixed;
  bottom: 15px;
  left: 0px; }
  .awardBadge2 a {
    -webkit-transition: 0.3s ease-in all;
    -o-transition: 0.3s ease-in all;
    transition: 0.3s ease-in all; }
  .awardBadge2 a img {
    width: 70px; }
  .awardBadge2 a:hover {
    opacity: 0.7; }

@media (max-width: 900px) {
  .header {
    height: 39px;
    opacity: .7;
    background: #0F0F0F; }
  .burger {
    background: url("./assets/burger.svg");
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
    width: 50px;
    height: 40px; }
  .buttonBox {
    top: 7px;
    right: 5px; }
  .button {
    opacity: 1;
    font-size: 11px;
    padding: 3px 12px 5px 12px;
    margin: 0 3px; } }



