/* global variables */
.pictureCover {
  margin-top: 10px;
  overflow: hidden; }
  .pictureCover img {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 4px; }
  .pictureCover :global(.image-box) {
    height: 380px;
    background-color: #151515;
    overflow: hidden; }
  .pictureCover video {
    height: 100%;
    width: 100%;
    max-width: none;
    -o-object-fit: cover;
       object-fit: cover; }

