/* global variables */
.box {
  display: inline-block;
  vertical-align: top;
  width: 120.85714px;
  height: 139px;
  margin-right: 9px;
  border-radius: 4px;
  background-color: #151515;
  position: relative; }
  .box:last-child {
    margin-right: 0; }

.iconContainer {
  position: absolute;
  top: 18px;
  height: 66px;
  width: 66px;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%); }

.icon {
  position: absolute;
  width: 100%;
  height: 100%; }

.separator {
  display: inline-block;
  border-bottom: 1px solid #ffffff;
  opacity: 0.2;
  width: 10px;
  position: absolute;
  bottom: 35px;
  left: 18px; }

.label {
  position: absolute;
  bottom: 16px;
  left: 18px;
  line-height: 20.74px;
  font-size: 12.2px;
  color: white;
  letter-spacing: 0.3294px;
  font-weight: lighter;
  text-align: center;
  opacity: 0.6; }

@media (max-width: 900px) {
  .box {
    width: 82vw;
    /*width: calc(100vw - (9px * 2));*/ } }



