/* global variables */
.boxcontainer {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  margin-right: 9px;
  border-radius: 4px;
  background-color: #151515;
  position: relative;
  -webkit-transition: all ease 0.38s;
  -o-transition: all ease 0.38s;
  transition: all ease 0.38s;
  cursor: pointer; }
  .boxcontainer:after {
    content: '';
    display: block;
    padding-bottom: 100%; }
  .boxcontainer :hover {
    opacity: 0.7; }

.box, :global(.icon-box) {
  overflow: hidden; }

.iconContainer {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -60%);
      -ms-transform: translate(-50%, -60%);
          transform: translate(-50%, -60%); }

.label {
  width: 100%;
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  top: calc(100% - 20px);
  -webkit-transform: translateY(25px);
  -ms-transform: translateY(25px);
  transform: translateY(25px);
  line-height: 20.74px;
  font-size: 12.2px;
  color: white;
  letter-spacing: 0.3294px;
  font-weight: lighter;
  -webkit-transition: 0.17s ease-in all;
  -o-transition: 0.17s ease-in all;
  transition: 0.17s ease-in all;
  text-align: center;
  letter-spacing: 0.07px;
  opacity: 0.6; }

@media (max-width: 600px) {
  .box {
    width: 100%; }
  .icon {
    -webkit-transform: scale(0.65);
        -ms-transform: scale(0.65);
            transform: scale(0.65); } }



